height:150% !important}}.success-icon{font-size:8rem;color:#57b846}h1{color:#57b846;text-align:center;margin-top:-20px !important}.stats{background-color:#f8f8f8;border-radius:5px;padding-left:2rem;padding-right:2rem;padding-bottom:2rem;height:80%;width:35%}h4{text-align:center;padding:30px !important}h2{text-align:center;color:#1da1f2;margin-top:-10px !important}.leftButton{border:2px solid;color:#1da1f2;text-align:center;padding-left:1rem;padding-right:1rem;cursor:pointer;margin-right:30px !important}.leftButton a{transition:0.2s linear all}.leftButton:hover{background-color:#1da1f2}.leftButton:hover a{color:#fff}.rightButton{border:2px solid;color:#57b846;padding-left:1rem;padding-right:1rem;cursor:pointer;-webkit-text-decoration-color:#57b846;text-decoration-color:#57b846;text-align:center}.rightButton a{color:#57b846;transition:0.2s linear all}.rightButton:hover{background-color:#57b846}.rightButton:hover a{color:#fff}.summaryButtons{text-align:center !important;width:100%;display:flex;justify-content:center;margin-top:3rem}.no-stats{margin-top:50px !important}.questions{background-color:#f8f8f8;border-left:5px solid #1da1f2;width:80%;margin:3rem auto 0 auto;padding:1.5rem 2rem}.questions h2{text-align:center;font-weight:lighter}.questions .lifeline-container{display:flex;justify-content:space-between}.questions .lifeline{position:relative;top:-3px}.questions .lifeline-icon{color:#57b846;cursor:pointer}.questions .lifeline-icon:hover{color:#cc8400;background:none}.questions h5{font-size:1.2rem;margin-bottom:3rem;line-height:1.5;text-align:center}.questions .options-container{display:inline-block;width:50%}.questions .option{background-color:#1da1f2;border-radius:30px;color:#fff;cursor:pointer;margin:2rem auto;padding:1rem;transition:0.3s linear all;text-align:center;width:90%}.questions .option:hover{background-color:#0967a0}.button-container{display:flex;justify-content:flex-start;margin:0 auto 1.5rem auto;padding:2rem;width:80%}.button-container button{border